import config from "./config";
//************** 模块类接口****************************
//获取模块列表
const getModules = (params) => config.get("/Module/get_modules_v2/", params);
//*******************项目类,project开头的************************
//获取项目分类
const getProjectType = (params) =>
  config.get("/Project/get_project_types/", params);
//新建项目接口
const addProject = (params) => config.post("/Project/add_project/", params);
//  保存/更新 项目(算法方案)
const updateProject = (params) =>
  config.post("/Project/update_project/", params);
//获取算法方案列表
const getProjectList = (params) =>
  config.get("/Project/get_projects_by_page/", params);
//获取项目配置
const getProjectConfig = (params) =>
  config.get("/Project/get_project_config/", params);
//删除项目
const deleteProject = (params) =>
  config.post("/Project/delete_project/", params);
// 获取项目实时状态
const getProjectStatusRealtime = (params) =>
  config.get("/Project/get_project_status_realtime/", params);
//获取项目历史状态
const getProjectStatusAll = (params) =>
  config.get("/Project/get_project_status_all/", params);
//启动项目
const runProject = (params) => config.post("/Project/run_project/", params);
// 按关键字搜索项目
const searchProject = (params) =>
  config.get("/Project/get_projects_by_keyword/", params);
//******************************数据集接口************

// 获取分页数据
const getPageData = (params) => config.get("/Data/get_data_by_page/", params);
// 获取二级导航栏列表
const resultList = (params) => config.get("/Data/get_datasets/", params);
// 分页获取二级导航栏列表
const resultListV2 = (params) =>
  config.get("/Data/get_datasets_by_page/", params);
// 关键字搜索数据集
const searchData = (params) =>
  config.get("/Data/search_dataset_by_keyword/", params);
//   新建数据集
const createDataset = (params) => config.post("/Data/create_dataset/", params);
// 手动上传图片
const uploadImgs = (params) => config.post("/Data/upload_data_record/", params);
//手动上传标注文件
const uploadXmls = (params) => config.post("/Data/upload_label_file/", params);
// 下载zip文件
const downLoad = (params) => config.get("/Data/download_dataset/", params);
// 删除数据集
const deleteData = (params) => config.post("/Data/delete_dataset/", params);
// 在线标注一个样本
const createTag = (params) => config.post("/Data/upload_label_online/", params);
// 删除标注一个样本
const deleteTag = (params) => config.post("/Data/delete_label_record/", params);
//分页查询图片列表信息
const ImageList = (params) => config.get("/Data/get_image_by_page/", params);
//上传数据集标签分类
const uploadLabel = (params) =>
  config.post("/Data/upload_voc_classes/", params);
//读取数据集的标签类别
const showLabel = (params) => config.get("/Data/get_voc_classes/", params);
export default {
  addProject,
  updateProject,
  getProjectList,
  getProjectConfig,
  deleteProject,
  getProjectStatusRealtime,
  getProjectStatusAll,
  runProject,
  searchProject,
  getPageData,
  resultList,
  resultListV2,
  searchData,
  createDataset,
  uploadImgs,
  uploadXmls,
  downLoad,
  deleteData,
  createTag,
  deleteTag,
  ImageList,
  uploadLabel,
  showLabel,
  getModules,
  getProjectType,
};
