const state = {
  compTag: {
    currentTag: {},
    tags: [
      //   {
      //     itemName: "项目一",
      //     routeName: "ProjectManage",
      //     pageIndex: "",
      //     icon: "",
      //   },
    ],
  },
};
const mutations = {
  setCompTagTags(state, data) {
    state.compTag.currentTag = data;
    let result = state.compTag.tags.findIndex(
      (item) =>
        item.itemName === data.itemName && item.routeName === data.routeName
    );
    result === -1 ? state.compTag.tags.push({ ...data }) : "";
    // state.compTag[data["key"]].push(data["value"]);
  },
  // 传入标签项 删除一个标签页
  delCompTag(state, val) {
    let result = state.compTag.tags.findIndex(
      (item) => item.itemName === val.itemName
    );
    // state.tabsList.splice(result, 1)
    state.compTag.tags.splice(result, 1);
  },
  //给 CompTag对应的属性赋值
  setCompTag(state, data) {
    state.compTag[data["key"]] = data["value"];
  },
};
const actions = {};
const getters = {};

export default {
  namespaced: true,
  //存放数据
  state,
  // 同步操作
  mutations,
  // 异步操作
  actions,
  getters,
};
