const state = {
  // 选中一级菜单的类型
  currentType: "",
  // 对应的二级菜单栏
  menuList: [],
  // 被选中的二级菜单名称
  itemDate: "",
  //当前选中的下标
  itemIndex: "",
  //删除标志 false 不删除
  isDelect: false,
  // 新建列表数据
  addForm: "",
  // 已标注图片参数
  isTagImgList: {},
  // 未标注图片参数
  unTagImgList: {},
};
const mutations = {
  // 选中的一级菜单的类型
  currentType(state, data) {
    state.currentType = data;
    console.log(data);
  },
  // 两级菜单栏
  targetCheck(state, data) {
    state.menuList = data;
    console.log(data);
  },
  // 新建数据集  添加到二级菜单栏信息
  appendAarry(state, data) {
    // data["index"] = state.menuList.length;
    state.menuList.unshift(data);
    console.log(data);
  },

  // 被选中的二级菜单
  currentDate(state, data) {
    state.itemDate = data.itemDate;
    state.itemIndex = data.itemIndex;
    console.log(data);
  },
  // 删除后的二级菜单
  deleteData(state, data) {
    state.menuList = data;
    console.log(data);
  },
  // 获取后台参数
  paramList(state, data) {
    state.paramList = data;
    console.log(data);
  },
  // 获取标注页面的已标注图片参数
  isTagImgList(state, data) {
    state.isTagImgList = data;
    console.log(data);
  },
  // 获取标注页面的未标注图片参数
  unTagImgList(state, data) {
    state.unTagImgList = data;
    console.log(data);
  },
  // 当前选中图片信息
  // curImage(state, data) {
  //   state.curImage = data;
  //   console.log(data);
  // },
  // 点击删除标志
  delect_resultList_isDelect(state, data) {
    state.isDelect = data;
  },
  // 新建表单信息
  add_resultList_isAdd(state, data) {
    state.addForm = data;
  },
};
const actions = {};
const getters = {};

export default {
  namespaced: true,
  //存放数据
  state,
  // 同步操作
  mutations,
  // 异步操作
  actions,
  getters,
};
