// 节流
const throttle = function (fn, wait = 200) {
  let last, timer, now;
  return function () {
    now = Date.now();
    if (last && now - last < wait) {
      clearTimeout(timer);
      timer = setTimeout(function () {
        last = now;
        fn.call(this, ...arguments);
      }, wait);
    } else {
      last = now;
      fn.call(this, ...arguments);
    }
  };
};
// 防抖
const debounce = function (fn, wait = 200) {
  let timer;
  return function () {
    let context = this;
    let args = arguments;

    clearTimeout(timer);

    timer = setTimeout(() => {
      fn.apply(context, args);
    }, wait);
  };
};

export default {
  debounce,
  throttle,
};
