/* jshint esversion: 11 */
import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

// const routes = []
export default new Router({
  routes: [
    {
      path: "/Navigation",
      name: "Navigation",
      component: () => import("@/views/Navigation/NavigationPage.vue"),
      children: [
        {
          path: "ProjectManage",
          name: "ProjectManage",
          component: () =>
            import("@/views/Navigation/ProjectManage/ProjectManage.vue"),
        },
        {
          path: "Item",
          name: "Item",
          component: () => import("@/views/Navigation/Details/ItemDetails.vue"),
          children: [
            {
              path: "image",
              name: "image",
              component: () =>
                import("@/views/Navigation/Details/Childrens/ImagsDetails.vue"),
            },
            {
              path: "text",
              name: "text",
              component: () =>
                import("@/views/Navigation/Details/Childrens/TextsDetails.vue"),
            },
            {
              path: "video",
              name: "video",
              component: () =>
                import("@/views/Navigation/Details/Childrens/VideoDetails.vue"),
            },
            {
              path: "audio",
              name: "audio",
              component: () =>
                import("@/views/Navigation/Details/Childrens/AudioDetails.vue"),
            },
            {
              path: "remote_sensing",
              name: "remote_sensing",
              component: () =>
                import(
                  "@/views/Navigation/Details/Childrens/RemoteDetails.vue"
                ),
            },
            // {
            //   path: "tagImage",
            //   name: "tagImage",
            //   component: () =>
            //     import("@/views/Navigation/Details/Childrens/TagImage.vue"),
            // },
            {
              path: "Item",
              redirect: "image",
            },
          ],
        },
        {
          path: "/Navigation",
          redirect: "ProjectManage",
        },
      ],
    },

    {
      path: "/",
      redirect: "/Navigation",
    },
    {
      path: "/FlowChart",
      name: "FlowChart",
      component: () => import("@/views/FlowChart/FlowChart.vue"),
      // children: [],
    },
    // 标注页面
    {
      path: "/TagImage",
      name: "TagImage",
      component: () => import("@/views/TagImage/TagImage.vue"),
    },
  ],
});
