const state = {
  itemShow: {
    //项目数据
    projectJson: "",
    //当前选中节点数据
    currentNodeData: "",
    //项目配置参数
    runningConfig: "",
  },
  asideLeft: {
    projectType: {}, //选中的算法方案类型
    projectTypeList: [], //算法方案类型列表
  },
  resultList: {
    //当前项目列表
    curNavList: [],
    //当前选中方案
    curItem: {
      name: "",
      id: "",
      type: "",
      desc: "",
    },
    curIndex: 0, //当前选中方案的下标0
    isDelect: false, //删除标志 false 不删除
  },
  nodeParam: {
    // nodeType: "",
  },
  dialogFrom: {
    //新建项目弹窗组件
    dialogFormVisible: "",
    ruleForm: "",
  },
  open: true,
};
const mutations = {
  set_itemShow_projectJson(state, data) {
    state.itemShow.projectJson = data;
    // console.log(data);
  },
  setItemShow(state, data) {
    state.itemShow[data["key"]] = data["value"];
  },
  setAsideLeft(state, data) {
    state.asideLeft[data["key"]] = data["value"];
  },
  set_asideLeft_projectType(state, data) {
    state.asideLeft.projectType = data;
    // console.log(data);
  },
  setResultList(state, data) {
    state.resultList[data["key"]] = data["value"];
  },
  set_resultList_curItem(state, data) {
    state.resultList.curItem = data.curItem;
    state.resultList.curIndex = data.curIndex;
  },
  delect_resultList_isDelect(state, data) {
    state.resultList.isDelect = data;
  },
  set_nodeParam_nodeType(state, data) {
    state.nodeParam.nodeType = data;
  },
  set_dialogFrom(state, data) {
    state.dialogFrom = data;
  },
  set_dialogFrom_ruleForm(state, data) {
    state.dialogFrom.ruleForm = data;
  },
  set_dialogFrom_dialogFormVisible(state, data) {
    state.dialogFrom.dialogFormVisible = data;
  },
  add_resultList_addOneItem(state, data) {
    state.resultList.addOneItem = data;
  },
  setOpen(state, data) {
    state.open = !state.open;
  },
};
const actions = {};
const getters = {};

export default {
  namespaced: true,
  //存放数据
  state,
  // 同步操作
  mutations,
  // 异步操作
  actions,
  getters,
};
