const state = {
  //参数列表组件输入的参数
  paramList: {
    params: [], //参数列表
    modelName: "",
    isControlGraph: false, //是否可以操控画布 默认可以
    nodeData: {}, //节点的data属性
    isdisabled: true,
  },
  dialogFrom: {
    dialogFormVisible: false,
    projectBasicInfo: {
      project_id: 7,
      project_name: "", //项目名称
      project_desc: "", //项目描述
    },
  },
};
const mutations = {
  setParamList(state, data) {
    state.paramList[data["key"]] = data["value"];
    // console.log(data);
  },
  setDialogFrom(state, data) {
    console.log(data);
    state.dialogFrom[data["key"]] = data.value;
  },
};
const actions = {};
const getters = {};

export default {
  namespaced: true,
  //存放数据
  state,
  // 同步操作
  mutations,
  // 异步操作
  actions,
  getters,
};
