import axios from "axios";

const service = axios.create({
  //请求超时时间
  timeout: 20000,
  //基础路径
  baseURL: config_server.path,
});
// console.log(config_server.path);
service.interceptors.request.use(
  (config) => {
    return config;
  },
  (err) => {
    console.log(err);
  }
);
//响应拦截器
service.interceptors.response.use(
  (response) => {
    let res = {};
    res.status = response.status;
    res.data = response.data;
    return res;
  },
  (err) => console.log(err)
);

const get = function (url, params, timeout) {
  return new Promise((resolve, reject) => {
    service
      .get(url, {
        params: params,
        timeout: timeout,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
};
const post = function (url, params, timeout) {
  return new Promise((resolve, reject) => {
    service
      .post(url, params, {
        timeout: timeout,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
};
const websocket = function (url) {
  let path = config_server.path.split("//")[1];
  // let path = '8.134.211.173:8686'
  return new WebSocket("ws://" + path + url);
};
export default {
  service,
  get,
  post,
  websocket,
};
